import React, { useState, useEffect, useCallback } from 'react';
import {
  Box, Flex,
} from 'rebass';
import CarouselItem from './CarouselItem';
import CarouselDot from './CarouselDot';

import carouselData from '../assets/carouselData';

const CAROUSEL_WIDTH = 454;
const CAROUSEL_DURATION = 6000;

const Carousel = () => {
  // Store which carousel item is active
  const [activeIndex, setActiveIndex] = useState(0);
  // Store a timer to automatically advance the carousel
  const [timer, setTimer] = useState(null);
  // Store a boolean to allow us to not animate the carousel during resize
  const [isResizing, setIsResizing] = useState(false);

  const advanceSlider = () => {
    setActiveIndex((index) => (index + 1 >= carouselData.length ? 0 : index + 1));
  };

  const stopCarousel = useCallback(() => clearInterval(timer), [timer]);

  const startCarousel = useCallback(() => {
    setTimer(setInterval(advanceSlider, CAROUSEL_DURATION));
    return stopCarousel;
  }, [stopCarousel]);

  useEffect(() => {
    // Reset resizing boolean
    if (isResizing) {
      setTimeout(() => setIsResizing(false), 1000);
    }
  }, [isResizing]);

  useEffect(() => {
    startCarousel();

    // Set resizing boolean so that we can disable animation duration
    window.addEventListener('resize', () => {
      setIsResizing(true);
    });
    return () => {
      window.removeEventListener('resize');
    };
  }, []); // eslint-disable-line

  return (
    <Box
      data-testid="carousel"
      variant="carousel"
      onMouseEnter={stopCarousel}
      onMouseLeave={startCarousel}
      onClick={advanceSlider}
      sx={{
        width: CAROUSEL_WIDTH,
        position: 'relative',
        overflow: 'hidden',
        display: ['none', 'none', 'none', 'block'],
        cursor: 'pointer',
      }}
    >
      <Flex sx={{
        transition: isResizing ? 'transform 0s' : 'transform 0.5s',
        transform: `translate3d(-${CAROUSEL_WIDTH * activeIndex}px, 0, 0)`,
        position: 'absolute',
        top: 0,
        bottom: 0,
        left: 0,
        width: CAROUSEL_WIDTH * carouselData.length,
      }}
      >
        {carouselData.map(
          (item) => <CarouselItem key={item.id} item={item} />,
        )}
      </Flex>
      <Flex sx={{
        position: 'absolute', left: 32, bottom: 32, zIndex: 1,
      }}
      >
        {carouselData.map((item, index) => (
          <CarouselDot
            key={index}
            index={index}
            activeIndex={activeIndex}
            setActiveIndex={setActiveIndex}
          />
        ))}
      </Flex>
    </Box>
  );
};

export default Carousel;
