import React, { useEffect, useState, useRef } from 'react';
import { Box, Text, Flex } from 'rebass';
import Player from '@vimeo/player';
import { motion, AnimatePresence } from 'framer-motion';
import { ReactComponent as CloseIcon } from '../assets/close.svg';

const Video = ({ isVideoOpen, setIsVideoOpen }) => {
  const [isVideoReady, setIsVideoReady] = useState(false);

  const player = useRef();

  useEffect(() => {
    player.current = new Player('video');

    player.current.on('play', () => {
      console.log('played the video!');
    });
    player.current.on('load', () => {
      console.log('loaded the video!');
    });
    player.current.on('ready', () => {
      console.log('video is ready!');
    });
    player.current.on('pause', () => {
      console.log('video is paused!');
    });
    player.current.on('ended', () => {
      setIsVideoOpen(false);
    });

    setIsVideoReady(true);
  }, [setIsVideoOpen]);

  useEffect(() => {
    if (isVideoReady && isVideoOpen) {
      player.current.play();
    }
  }, [isVideoReady, isVideoOpen]);

  return (
    <Box
      backgroundColor="darkNavy"
      title="Return to previous screen"
      sx={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        zIndex: 3,
        gridTemplateRows: 'auto 1fr',
        display: 'grid',
        opacity: isVideoOpen ? 1 : 0,
        pointerEvents: isVideoOpen ? 'auto' : 'none',
        animation: 'fadeIn 1s',
        cursor: 'pointer',
      }}
      onClick={() => setIsVideoOpen(false)}
    >
      <motion.div
        animate={{ translateY: isVideoOpen ? 0 : -200, opacity: isVideoOpen ? 1 : 0 }}
        transition={{ duration: 0.5, ease: 'easeInOut' }}
      >
        <Box
          px={[7]}
          py={[6]}
        >
          <Text variant="videoTitle" mb={2}>Setting up your account ...</Text>
          <Text variant="videoDescription">
            Please wait while we configure and set your account. In the meanwhile, please watch our
            product overview video
          </Text>
          <Box sx={{
            position: 'absolute', top: 40, right: 40, width: 22, height: 22,
          }}
          >
            <CloseIcon />
          </Box>
        </Box>
      </motion.div>
      <Flex justifyContent="flex-start" backgroundColor="liveNavy" sx={{ position: 'relative' }}>
        <AnimatePresence>
          <motion.iframe
            animate={{ opacity: isVideoOpen ? 1 : 0 }}
            transition={{ duration: 1, delay: 2 }}
            initial={{ opacity: 0 }}
            ref={player}
            id="video"
            src="https://player.vimeo.com/video/398468660?autoplay=0&amp;app_id=122963"
            frameBorder="0"
            allow="autoplay; fullscreen"
            allowFullScreen
            style={{
              position: 'absolute',
              top: 0,
              left: 0,
              width: '100%',
              height: '100%',
            }}
            title="FTUE"
          />
        </AnimatePresence>
      </Flex>
    </Box>
  );
};

export default Video;
