import React, { useEffect, useState, useRef } from 'react';
import { Box } from 'rebass';
import { Input } from '@rebass/forms';
import InputFieldErrors from './InputFieldErrors';

const InputField = ({
  name, placeholder, value, updateValue, error, isFirst,
}) => {
  const [isFocused, setIsFocused] = useState(true);
  const inputRef = useRef();

  useEffect(() => {
    // Focus the first input field onload
    if (isFirst) { inputRef.current.focus(); }
  }, [isFirst]);

  return (
    <Box pl={[3, 6, 9]} pr={[3, 6, 0]} sx={{ position: 'relative' }}>
      <Input
        ref={inputRef}
        width={['100%', '100%', 414]}
        variant={(!error.isValid) ? 'textInputError' : 'textInput'}
        type={name === 'password' ? 'password' : 'text'}
        placeholder={placeholder}
        mb={error.isValid ? 4 : 2}
        value={value}
        onFocus={() => setIsFocused(true)}
        onBlur={() => setIsFocused(false)}
        onChange={(event) => updateValue(name, event.target.value)}
        sx={{
          '&:focus': {
            outlineColor: 'blue',
            outlineWidth: 2,
            outlineStyle: 'auto',
          },
        }}
      />
      <InputFieldErrors name={name} focused={isFocused} error={error} />
    </Box>
  );
};

export default InputField;
