import React from 'react';
import { Image } from 'rebass';
import LivePersonLogo from '../assets/logo.svg';

const Logo = ({ isMobileSize }) => (
  <Image
    src={LivePersonLogo}
    alt="LivePerson logo"
    px={isMobileSize ? 2 : 3}
    py={isMobileSize ? 3 : 6}
    sx={{
      display: isMobileSize ? ['block', 'block', 'none'] : ['none', 'none', 'block'],
      position: ['static', 'static', 'absolute'],
      top: 0,
      left: 0,
      zIndex: 2,
      width: isMobileSize ? 170 : 215,
    }}
  />
);

export default Logo;
