import React from 'react';
import {
  Box,
} from 'rebass';
import { Select } from '@rebass/forms';

const getOptions = (name) => {
  switch (name) {
    case 'country':
      return [
        { value: '', name: 'Choose a country', disabled: true },
        { value: 'z1', name: 'The Americas' },
        { value: 'z2', name: 'Europe, Middle East, and Africa' },
        { value: 'z3', name: 'Asia Pacific' },
      ];
    case 'organizationType': {
      return [
        { value: '', name: 'Choose a type', disabled: true },
      ];
    }
    default:
      return [];
  }
};

const SelectField = ({
  name, value, updateValue, error,
}) => (
  <Box pl={[3, 6, 9]} pr={[3, 6, 0]} sx={{ position: 'relative' }}>
    <Select
      width={['100%', '100%', 300, 414]}
      variant="textInput"
      value={value}
      onChange={(event) => updateValue(name, event.target.value)}
      name="country"
      mb={error.isValid ? 4 : 2}
      sx={{
        '&:focus': {
          outlineColor: 'blue',
          outlineWidth: 2,
          outlineStyle: 'auto',
        },
      }}
    >
      {getOptions(name).map((option) => (
        <option key={option.value} value={option.value} disabled={option.disabled}>
          {option.name}
        </option>
      ))}
    </Select>
  </Box>
);

export default SelectField;
