import React, { useState, useRef } from 'react';
import { Box, Button } from 'rebass';
import ReCAPTCHA from 'react-google-recaptcha';
import InputField from './InputField';
import SelectField from './SelectField';
import Title from './Title';
import Video from './Video';
import InputFieldErrors from './InputFieldErrors';

const { REACT_APP_RECAPTCHA_KEY } = process.env;

const Wrapper = ({ children }) => <Box ml={[3, 6, 9]} mr={[3, 6, 0]}>{children}</Box>;

const ReCaptcha = ({ updateValue, error, recaptchaRef }) => (
  <Wrapper>
    <ReCAPTCHA
      ref={recaptchaRef}
      size="normal"
      sitekey={REACT_APP_RECAPTCHA_KEY}
      onChange={() => updateValue('recaptcha', true)}
      onErrored={() => console.log('ReCAPTCHA failed')}
      onExpired={() => console.log('ReCAPTCHA expired')}
    />
    {!error.isValid && <InputFieldErrors name="recaptcha" focused error={error} />}
  </Wrapper>
);

const Form = () => {
  const [isVideoOpen, setIsVideoOpen] = useState(false);
  const [values, setValues] = useState(
    {
      firstName: '',
      lastName: '',
      email: '',
      password: '',
      organizationName: '',
      organizationType: '',
      country: '',
      recaptcha: true,
    },
  );
  const [errors, setErrors] = useState(
    {
      firstName: {
        isValid: true,
        touched: false,
        errorMessage: 'Required field',
      },
      lastName: {
        isValid: true,
        touched: false,
        errorMessage: 'Required Field',
      },
      email: {
        isValid: true,
        touched: false,
        errorMessage: 'The business email address entered is not valid.',
        requirements: 'We only accept business email addresses. Email services like gmail, ymail, etc are not valid.',
      },
      password: {
        isValid: true,
        touched: false,
        errorMessage: 'Your password does not meet all the requirements',
        requirements: {
          name: 'Password requirements',
          data: [
            { name: '- at least 8 characters long ' },
            { name: '- at least one uppercase letter', isError: true },
            { name: '- at least one number', isError: true },
            { name: '- at least one of these special characters:', isError: true },
            { name: '&, @, !, *, $,<, >,  /, (,), %, #, @, !, =', shouldIndent: true },
          ],
        },
      },
      organizationName: {
        isValid: true,
        touched: false,
        errorMessage: 'Required Field',
      },
      organizationType: {
        isValid: true,
        touched: false,
        errorMessage: 'Required Field',
      },
      country: {
        isValid: true,
        touched: false,
        errorMessage: 'Required Field',
      },
      recaptcha: {
        isValid: true,
        touched: false,
        errorMessage: 'Required Field',
      },
    },
  );
  const recaptchaRef = useRef();

  const setErrorIsValid = (name, value) => {
    setErrors((prevErrors) => ({
      ...prevErrors,
      [name]: {
        ...prevErrors[name],
        isValid: value,
      },
    }));
  };

  const setErrorTouched = (name, value) => {
    setErrors((prevErrors) => ({
      ...prevErrors,
      [name]: {
        ...prevErrors[name],
        touched: value,
      },
    }));
  };

  const validateValue = (name, value) => {
    // console.log('name', name);
    // console.log('name', value);
    switch (name) {
      case 'email':

        const emailPattern = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}/;
        const isEmail = emailPattern.test(value);
        isEmail ? setErrorIsValid(name, true) : setErrorIsValid(name, false);
        break;
      case 'password':
        // password stuff
        if (value && value.length > 6) {
          setErrorIsValid(name, true);
        }

        break;
        // case 'firstName':
        // case 'lastName':
        //   (values.firstName !== '' && errors[name].touched) || (values.lastName !== '' && errors[name].touched)
        //     ? setErrorIsValid(name, true)
        //     : setErrorIsValid(name, false);

      //   break;
      case 'recaptcha':
        setErrorIsValid(name, true);
        break;
      default:
        value !== ''
          ? setErrorIsValid(name, true)
          : setErrorIsValid(name, false);
    }
  };

  const updateValue = (name, value) => {
    setValues({ ...values, [name]: value });
    validateValue(name, value);
  };

  const hasErrors = () => Object.values(errors).reduce((acc, val) => acc && val, false);

  const validateAllValues = () => {
    Object.keys(values).map((item) => validateValue(item, values.item));
  };

  const sendAccountData = async () => fetch(process.env.REACT_APP_ENDPOINT_ALPHA, {
    method: 'POST',
    headers: { 'x-api-key': process.env.REACT_APP_API_KEY, 'Content-Type': 'application/json', Accept: 'application/json' },
    body: JSON.stringify({ ...values, recaptchaKey: recaptchaRef.current.getValue() }),
  }).then((response) => response.json()).catch((error) => console.log(error));

  const createAccount = () => {
    // Assuming that here are no validation errors
    validateAllValues();

    // NOTE: disabling this because we
    sendAccountData().then(() => setIsVideoOpen(true));

    // Open the video
  };

  return (
    <Box sx={{ position: 'relative' }}>
      <Wrapper>
        <Title />
      </Wrapper>
      <form data-testid="registerForm">
        <InputField
          name="firstName"
          value={values.firstName}
          updateValue={updateValue}
          placeholder="First name"
          error={errors.firstName}
          isFirst
        />
        <InputField
          name="lastName"
          value={values.lastName}
          updateValue={updateValue}
          placeholder="Last name"
          error={errors.lastName}
        />
        <InputField
          name="email"
          value={values.email}
          updateValue={updateValue}
          placeholder="Enter business email address"
          error={errors.email}
        />
        <InputField
          name="password"
          value={values.password}
          updateValue={updateValue}
          placeholder="Create password"
          error={errors.password}
        />
        <InputField
          name="organizationName"
          value={values.organizationName}
          updateValue={updateValue}
          placeholder="Organization Name"
          error={errors.organizationName}
        />
        <SelectField
          name="organizationType"
          value={values.organizationType}
          updateValue={updateValue}
          error={errors.organizationType}
        />
        <SelectField
          name="country"
          value={values.country}
          updateValue={updateValue}
          error={errors.country}
        />
      </form>
      <ReCaptcha updateValue={updateValue} error={errors.recaptcha} recaptchaRef={recaptchaRef} />
      <Wrapper>
        <Button data-testid="registerButton" mt={3} onClick={createAccount}>
          Create Account
        </Button>
      </Wrapper>
      <Video isVideoOpen={isVideoOpen} setIsVideoOpen={setIsVideoOpen} />
    </Box>
  );
};

export default Form;
