import React from 'react';
import { Text } from 'rebass';

const RequiredItems = ({ items }) => Object.keys(items).map((item) => {
  switch (item) {
    case 'data':
      return (
        items.data.map((d) => (
          <Text
            key={d.name}
            variant={d.isError ? 'errorText' : 'errorInformation'}
            ml={d.shouldIndent ? 2 : 0}
          >
            {`${d.name}`}
          </Text>
        ))
      );
    case 'name':
      return (
        <Text key={items.name} variant="errorInformation">{`${items.name}:`}</Text>
      );
    default:
      return null;
  }
});

const InputFieldErrors = ({ focused, name, error }) => {
  const isRecaptcha = name === 'recaptcha';
  const ErrorText = () => {
    if (!error.isValid && !!error.errorMessage) {
      return (
        <Text
          variant="errorMessage"
          mb={3}
          mt={isRecaptcha ? 2 : 0}
        >
          {error.errorMessage}
        </Text>
      );
    }
    return null;
  };

  const Requirements = () => {
    // console.log(error);
    const isString = typeof error.requirements === 'string';
    // console.log("error req", focused)

    if (!error.isValid && focused && !!error.requirements) {
      return (
        <Text
          ml={6}
          mr={6}
          variant="errorInformation"
          sx={{
            overflow: 'visible',
            position: 'absolute',
            top: 0,
            left: '604px',
            right: 0,
            display: ['none', 'none', 'block', 'block'],
          }}
        >
          {isString ? error.requirements : <RequiredItems items={error.requirements} />}
        </Text>
      );
    }
    return null;
  };
  return (
    <>
      <ErrorText />
      <Requirements />
    </>
  );
};
export default InputFieldErrors;
